<template>
  <div class="col">
    <div class="row">
      <div class='col-md-5 ms-3'>
        <button class="btn btn-secondary botonesPrincipales btn-lg me-2 menusColorIvan"
          @click="borrar()"><i class="bi bi-calculator display-5"></i>
        </button>
      </div>
      <div class='col-md-2'>
        <button class="btn btn-secondary botonesPrincipales btn-lg me-2 menusColorIvan"
          @click="abrirModalTicketsAbiertos()">
          <i class="bi bi-eyeglasses display-5"></i>
        </button>
      </div>
    </div>
    <div class="row justify-content-end">
    </div>
    <div class="row justify-content-end">
      <router-link to='/cobro/100'
        class="btn btn-secondary  botonesPrincipales
         me-2 menusColorIvan btn-lg"
        @click="cobrar()"><i class="bi bi-cart display-6"></i>
        <span class='display-6'>&nbsp;{{getTotal}} </span>
      </router-link>
    </div>
  </div>
</template>

<script>
import { useStore } from 'vuex';

import { computed } from 'vue';

export default {
  name: 'MenuBotones',
  setup() {
    const store = useStore();
    const cesta = computed(() => store.state.Cesta.cesta);
    const lineaDeRegalo = null;
    const getTotal = computed(() => {
      let suma = 0;
      for (let i = 0; i < cesta.value.lista.length; i += 1) {
        if (i !== lineaDeRegalo) {
          suma += cesta.value.lista[i].subtotal;
        }
      }
      return suma.toFixed(2);
    });
    function cobrar() {
      console.log('COBRO');
    }

    function abrirModalTicketsAbiertos() {
      console.log('ABRIR');
    }

    return {
      getTotal,
      cobrar,
      abrirModalTicketsAbiertos,
    };
  },
};
</script>

<style scoped>
.sizeMenus{
    font-size: 42px;
}
.anchoBotonTotal {
  width: 420px;
}
.anchoBotonBorrar {
  width: 200px;
}
.anchoBotonMas {
  width: 200px;
}
.anchoBotonMenu {
  width: 125px;
  height: 100px;
  margin: 10px;
}
.botonesPrincipales{
  background-color: #fff5e9;
  color: #c95907;
  border-color: #bf5c18;
  border-width: 3px;
}
.menusColorIvan.btn-secondary:hover {
    color: #c95907 !important;
    background-color: #fff5e9 !important;
    border-color: #bf5c18 !important;
    border-width: 3px;
  }

.menusColorIvan.btn-secondary:focus, .btn-secondary.focus {
  color: #c95907 !important;
  background-color: #fff5e9 !important;
  border-color: #bf5c18 !important;
  box-shadow: none !important;
  border-width: 3px;
}

.menusColorIvan.btn-secondary:not(:disabled):not(.disabled):active,
.btn-secondary:not(:disabled):not(.disabled).active,
.show > .btn-secondary.dropdown-toggle {
  color: #c95907 !important;
  background-color: #fff5e9 !important;
  border-color: #bf5c18 !important;
  border-width: 3px;
}

.menusColorIvan.btn-secondary:not(:disabled):not(.disabled):active:focus,
.btn-secondary:not(:disabled):not(.disabled).active:focus,
.show > .btn-secondary.dropdown-toggle:focus {
  box-shadow: none !important;
}

.iconosBootstrap {
