<template>
  <div class='row pt-1' v-bind:class='{hidden: isHidden}'>
    <div class='col-md-1'>
      <div class='sidebar w-auto'>
        <router-link to='/' @click='hideMenu'>Vender</router-link>
        <router-link to='/menu/caja'>Caja</router-link>
        <router-link to='/menu/pedidos' @click='quitarActivoTicket'>Pedidos</router-link>
        <router-link to='/menu/fichajes' @click='quitarActivoTicket'>Fichajes</router-link>
        <router-link to='/menu/devoluciones' @click='quitarActivoTicket'>Devoluciones</router-link>
        <router-link to='/' @click='imprimirEntregas'>Entregas</router-link>
        <hr />
        <Dependientas nombreCompleto='Santiago lvarez Osuca' id='156' />
        <Dependientas nombreCompleto='Santiago lvarez Osuca' id='156' />
      </div>
    </div>
    <div class='col'>
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import { computed } from 'vue';
import { useStore } from 'vuex';
import Dependientas from '@/components/Dependientas.vue';
import axios from 'axios';

export default {
  components: { Dependientas },
  name: 'Menu',
  setup() {
    const store = useStore();
    const isHidden = computed(() => store.state.Menu.hidden);
    function quitarActivoTicket() {
      store.dispatch('Ticket/setActivoAction', null);
    }
    function hideMenu() {
      store.dispatch('Menu/setHiddenAction', true);
      quitarActivoTicket();
    }
    function imprimirEntregas() {
      // const licencia = this.getNumeroTresDigitos(this.getParametros().licencia);
      const licencia = 842;
      axios.get(`http://dsv.hiterp.com/TpvInforma.asp?Llic=00${licencia}&Versio=6001010&Tipus=EntregasPendientes`).then((response) => {
        const { data } = response;
        let imprimir = '';
        let valid = false;
        // eslint-disable-next-line no-plusplus
        for(let i = 0; i < data.length; i++) {
            if(valid || (data[i - 1] === ']' && data[i - 2] === 'a')) {
                valid = true;
                if(data[i] === ']') break;
                imprimir += data[i];
            }
        }
        // Mandar al backend para imprimir
        console.log(imprimir);
      });
      hideMenu();
      quitarActivoTicket();
    }
    return {
      isHidden,
      hideMenu,
      quitarActivoTicket,
      imprimirEntregas,
    };
  },
};
</script>
<style scoped>
.sidebar {
  margin: 0;
  padding: 0;
  background-color: #f1f1f1;
  position: fixed;
  height: 60%;
  overflow: scroll;
}
.sidebar a {
  display: block;
  color: black;
  padding: 16px;
  text-decoration: none;
}
.sidebar a.active {
  background-color: #04AA6D;
  color: white;
}
.sidebar a:hover:not(.active) {
  background-color: #555;
  color: white;
}
.component-content {
  margin: 0 auto;
  width: 65%;
}
.hidden {
  display: none;
}
@media screen and (max-width: 700px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar a {float: left;}
  div.content {margin-left: 0;}
}
@media screen and (max-width: 400px) {
  .sidebar a {
    text-align: center;
    float: none;
  }
}
</style>>
